/**
 * Ëé∑ÂèñÂΩìÂâçÊó∂Èó¥ÂáèÂéªÊåáÂÆöÂ∞èÊó∂ÂêéÁöÑÊó∂Èó¥Êà≥ÔºàÂà∞ÁßíÔºâ
 * @param hoursToSubtract Ë¶ÅÂáèÂéªÁöÑÂ∞èÊó∂Êï∞
 * @returns Êó∂Èó¥Êà≥ÔºàÁßíÔºâ
 */
export function getTimestampMinusHours(hoursToSubtract: number = 4): number {
  // Ëé∑ÂèñÂΩìÂâçÊó∂Èó¥ÔºàÊØ´ÁßíÔºâ
  const now = new Date().getTime()

  // ËÆ°ÁÆó4Â∞èÊó∂ÁöÑÊØ´ÁßíÊï∞
  const fourHoursInMs = hoursToSubtract * 60 * 60 * 1000

  // ËÆ°ÁÆóÂáèÂéª4Â∞èÊó∂ÂêéÁöÑÊó∂Èó¥ÔºàÊØ´ÁßíÔºâ
  const timeMinusFourHours = now - fourHoursInMs

  // ËΩ¨Êç¢‰∏∫ÁßíÂπ∂ÂèñÊï¥
  return Math.floor(timeMinusFourHours / 1000)
}

// Ë∑ùÁ¶ªÊó∂Èó¥ÔºàÁº©ÂÜôÁâàÔºâ
export function getTimeDifferenceAbbr(timestamp: number, now = Date.now()) {
  // ÂΩìÂâçÊó∂Èó¥Êà≥ÔºàÊØ´ÁßíÔºâ

  // ËÆ°ÁÆóÂ∑ÆÂÄºÔºàÊØ´ÁßíÔºâ
  const diffMs = now - timestamp

  // ËΩ¨Êç¢‰∏∫Áßí
  const diffSeconds = Math.floor(diffMs / 1000)

  // ËÆ°ÁÆóÂêÑ‰∏™Êó∂Èó¥Âçï‰Ωç
  const seconds = diffSeconds % 60
  const minutes = Math.floor(diffSeconds / 60) % 60
  const hours = Math.floor(diffSeconds / 3600) % 24
  const days = Math.floor(diffSeconds / 86400) % 30 // ÊØèÊúàÊåâ30Â§©ËÆ°ÁÆó
  const months = Math.floor(diffSeconds / (86400 * 30)) % 12
  const years = Math.floor(diffSeconds / (86400 * 365))

  // ÊûÑÂª∫ÁªìÊûúÂ≠óÁ¨¶‰∏≤
  let result = ''

  if (years > 0) {
    result += `${years}y `
    // return `${years}y`
  }

  if (months > 0) {
    if (result) {
      return result + `${months}mo `
    }
    result += `${months}mo `
  }

  if (days > 0) {
    if (result) {
      return result + `${days}d `
    }
    result += `${days}d `
    // return `${days}d`
  }

  if (hours > 0) {
    if (result) {
      return result + `${hours}h `
    }
    result += `${hours}h `
    // return `${hours}h`
  }

  if (minutes > 0) {
    if (result) {
      return result + `${minutes}m `
    }
    result += `${minutes}m `
    // return `${minutes}m`
  }

  if (seconds > 0) {
    result += `${seconds}s `
    // return `${seconds}s`
  }

  // Â¶ÇÊûúÁªìÊûú‰∏∫Á©∫ÔºàÊó∂Èó¥Êà≥Âú®Êú™Êù•ÔºâÔºåËøîÂõû"ÂàöÂàö"
  if (result === '') {
    return '0s '
  }
  // console.log('üöÄ ~ getTimeDifferenceAbbr ~ result:', result)

  return result + 'ago'
}

export function formatTimeDifference(timestamp: number): string {
  const now = new Date()
  const targetDate = new Date(timestamp)

  // ËÆ°ÁÆóÊó∂Èó¥Â∑ÆÔºàÊØ´ÁßíÔºâ
  const timeDiff = Math.abs(now.getTime() - targetDate.getTime())

  // ËΩ¨Êç¢‰∏∫‰∏çÂêåÊó∂Èó¥Âçï‰Ωç
  const seconds = Math.floor(timeDiff / 1000)
  const minutes = Math.floor(seconds / 60)
  const hours = Math.floor(minutes / 60)
  const days = Math.floor(hours / 24)

  // Ê†πÊçÆÊó∂Èó¥Â∑ÆÈÄâÊã©ÂêàÈÄÇÁöÑÂçï‰Ωç
  if (days >= 1) {
    return `${days}d`
  } else if (hours >= 1) {
    return `${hours}h`
  } else if (minutes >= 1) {
    return `${minutes}m`
  } else {
    return `${seconds}s`
  }
}
